/* Memory Buttons Wrapper */

#mainMemoryBtnWrapper {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  column-gap: 3px;

  & > button {
    position: relative;
    top: 0;
    padding: var(--space-s) var(--space-r);
    border: 0;
    background: transparent;

    color: var(--color-text);
    font-size: .75rem;

    transition-duration: var(--time-short);

    @include media-mobile-s-only {
      font-size: 1rem;
      padding: var(--space-r) var(--space-r);
    }
  }
}
